import React from "react";
import { Typography } from "@material-ui/core";
import { useQuery, UseQueryResult } from "react-query";
import { useSelector } from "react-redux";
import { styled } from "@material-ui/core/styles";
import Carousel from "react-elastic-carousel";

import {
  RootAppState,
  SupportedGoalsList,
} from "../../../../../utils/typeHelpers";

import Support from "./Support";
import MyArrow from "../../../../../components/MyArrow";
import CustomPagination from "../../../../../components/CustomPagination";
import useAxios from "../../../../../utils/useAxios";

const SupportedGoalsRow = () => {
  const userState = useSelector((state: RootAppState) => state.AuthReducer);

  const axiosInstance = useAxios();

  const {
    data: supportedGoalsData,
    isLoading: isLoading,
    isError: isError,
    error: error,
  }: UseQueryResult<SupportedGoalsList[], Error> = useQuery(
    ["PopularGoalsListData", userState.userInfo.access],
    async () => {
      const response = await axiosInstance.get(`/supported-goals/`);

      return response.data;
    }
  );

  return (
    <>
      {!isLoading &&
        !isError &&
        supportedGoalsData &&
        supportedGoalsData?.length > 0 && (
          <>
            <HeaderLeftBar>Goals you supported</HeaderLeftBar>
            <Carousel
              renderPagination={({ pages, activePage, onClick }) => (
                <CustomPagination
                  pages={pages}
                  activePage={activePage}
                  onClick={onClick}
                />
              )}
              isRTL={false}
              itemsToShow={1}
              renderArrow={MyArrow}
            >
              {supportedGoalsData.map((item: SupportedGoalsList, index) => (
                <Support item={item} key={index} />
              ))}
            </Carousel>
          </>
        )}
    </>
  );
};

export default SupportedGoalsRow;

export const HeaderLeftBar = styled(Typography)(() => ({
  color: "#212529",
  fontSize: 24,
  fontWeight: "bold",
}));
