import { Container } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
//
import LeftColumn from "./components/LeftColumn";
import RightColumn from "./components/RightColumnGoalView";

const LandingLoggedIn = () => {
  return (
    <div
      style={{
        backgroundColor: "#f9f9f9",
        overflow: "overlay",
        height: "100%",
        width: "100%",
        position: "relative",
      }}
    >
      <Container>
        <Grid container>
          <Grid item xs={12} sm={12} md={5} sx={{ backgroundColor: "#fff" }}>
            <LeftColumn />
          </Grid>
          <Grid item xs={12} sm={12} md={7} sx={{ backgroundColor: "#f9f9f9" }}>
            <RightColumn />
          </Grid>
        </Grid>
      </Container>
    </div>
  );
};

export default LandingLoggedIn;
