import React from "react";
import Scrollbar from "../../../../../components/Scrollbar";
import { useWindowDimensions } from "../../../../../utils/helperFunction";

const CustomScroller: React.FC = (props) => {
  const { width } = useWindowDimensions();
  return (
    <div>
      {width > 500 ? (
        <Scrollbar
          sx={{
            height: "100vh",
            "& .simplebar-content": {
              height: "100%",
              display: "flex",
              flexDirection: "column",
            },
          }}
        >
          {props.children}
        </Scrollbar>
      ) : (
        <div>{props.children}</div>
      )}
    </div>
  );
};

export default CustomScroller;
