import React, { useRef, useState, useEffect } from "react";
import { Grid, Stack, Typography } from "@material-ui/core";
import Carousel from "react-elastic-carousel";

import { useStyles } from "../styles";
import SearchAppBar from "./SearchFields";

import { useWindowDimensions } from "../../../../../utils/helperFunction";
import { styled } from "@material-ui/styles";

import {
  SdgsList,
  PopularGoalsList,
  RootAppState,
  AddFunction,
} from "../../../../../utils/typeHelpers";

import { useQuery, UseQueryResult } from "react-query";
import { useNavigate } from "react-router";
import ImageCard from "./ImageCard";
import { useSelector } from "react-redux";
import SupportedGoalsRow, { HeaderLeftBar } from "./SupportedGoalsRow";
import MyArrow from "../../../../../components/MyArrow";
import CustomPagination from "../../../../../components/CustomPagination";
import useAxios from "../../../../../utils/useAxios";
import PageErrorBoundary from "../../../../Errror/PageErrorBoundary";
import { ErrorBoundary } from "react-error-boundary";

const LeftColumn = () => {
  const classes = useStyles();
  const itemWidth = 100;

  const GoalCategoryCard = styled(Stack)(() => ({
    borderRadius: 5,
    maxWidth: itemWidth,
  }));

  const { width } = useWindowDimensions();

  const axiosInstance = useAxios();

  const refError = useRef<null | AddFunction>(null);
  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const { data, isError }: UseQueryResult<SdgsList[], Error> = useQuery(
    ["SdgsListData"],
    async () => {
      const response = await axiosInstance.get(`/sdgs-list/`);

      return response.data;
    }
  );

  const {
    data: popularGoalsData,

    isError: isError2,
  }: UseQueryResult<PopularGoalsList[], Error> = useQuery(
    ["PopularGoalsListData"],
    async () => {
      const response = await axiosInstance.get(`/popular-goals/`);

      return response.data;
    }
  );

  useEffect(() => {
    if (isError) {
      handleCreateError("API Error");
    }

    if (isError2) {
      handleCreateError("API Error");
    }
  }, [isError, isError2]);

  let navigate = useNavigate();
  const [searchTerm, setSearchTerm] = useState("");
  const [explode, setExplode] = useState(false);
  const handleSearch = () => navigate("../search", { state: { searchTerm } });
  const userState = useSelector((state: RootAppState) => state.AuthReducer);

  return (
    <ErrorBoundary
      fallbackRender={({ error, resetErrorBoundary }) => (
        <PageErrorBoundary
          error={error}
          resetErrorBoundary={resetErrorBoundary}
        />
      )}
      onReset={() => setExplode(false)}
      resetKeys={[explode]}
    >
      {explode ? null : (
        <Stack
          direction="column"
          justifyContent="center"
          alignItems="flex-start"
          spacing={3}
          sx={{
            padding: 3,
          }}
        >
          <div>
            <Typography
              style={{ fontSize: 40, color: "#212529", width: 250, height: 50 }}
              variant="h5"
            >
              People
            </Typography>
            <Grid container direction="row">
              <Typography
                variant="h5"
                className={classes.headerLeftSub}
                style={{ width: 250, height: 50 }}
              >
                empowering{" "}
              </Typography>
              <Typography style={{ fontSize: 40, height: 50 }} variant="h5">
                people
              </Typography>
            </Grid>
          </div>
          <SearchAppBar
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            startSearch={handleSearch}
          />
          <HeaderLeftBar>Browse Goal by Category</HeaderLeftBar>
          <Carousel
            isRTL={false}
            itemsToShow={width < 400 ? 2 : 3}
            renderArrow={MyArrow}
            renderPagination={({ pages, activePage, onClick }) => (
              <CustomPagination
                pages={pages}
                activePage={activePage}
                onClick={onClick}
              />
            )}
          >
            {data &&
              data.map((item: SdgsList, index) => (
                <GoalCategoryCard
                  direction="column"
                  justifyContent="center"
                  alignItems="center"
                  key={`${index}`}
                  onClick={() =>
                    navigate("../singlecategory", { state: { item: item } })
                  }
                  style={{ cursor: "pointer" }}
                >
                  <img
                    src={item.thumbnail !== null ? item.thumbnail : ""}
                    alt="cateImage"
                    className={classes.categoryImage}
                  />
                </GoalCategoryCard>
              ))}
          </Carousel>

          <HeaderLeftBar>Popular Goals</HeaderLeftBar>
          <Carousel
            isRTL={false}
            itemsToShow={width < 500 ? 2 : 3}
            renderArrow={MyArrow}
            renderPagination={({ pages, activePage, onClick }) => (
              <CustomPagination
                pages={pages}
                activePage={activePage}
                onClick={onClick}
              />
            )}
          >
            {popularGoalsData !== undefined &&
              popularGoalsData.map((item: PopularGoalsList) => (
                <div key={item.slug} style={{ margin: 10 }}>
                  <ImageCard item={item} />
                </div>
              ))}
          </Carousel>

          {userState.isLoggedIn && <SupportedGoalsRow />}
        </Stack>
      )}
    </ErrorBoundary>
  );
};

export default LeftColumn;
