import React from "react";
import {
  Box,
  CircularProgress,
  Grid,
  Stack,
  Typography,
  circularProgressClasses,
} from "@material-ui/core";
import { useNavigate } from "react-router";
import { styled } from "@material-ui/core/styles";

import { SupportedGoalsList } from "../../../../../utils/typeHelpers";
import { useStyles } from "../styles";
import "./styles.css";
import { useWindowDimensions } from "../../../../../utils/helperFunction";

const Support = ({ item }: { item: SupportedGoalsList }) => {
  const { width } = useWindowDimensions();

  const classes = useStyles();
  const navigate = useNavigate();
  const seeMore = () => navigate(`../viewgoal/${item.slug}`);
  return (
    <ContainerRoot
      direction="column"
      justifyContent="flex-start"
      sx={{
        minWidth: width > 1200 ? width * 0.27 : 330,
        maxWidth: width > 1200 ? width * 0.27 : 330,
      }}
    >
      <Stack direction="row" justifyContent="space-around" alignItems="center">
        <Grid item md={8} sm={12}>
          <Label>{item.title}</Label>
          <Typography className={classes.endorsedby}>
            Endorsed by {item.profile.ngo_username}
          </Typography>
        </Grid>
        <Grid item md={4} sm={12}>
          <Box sx={{ position: "relative", display: "inline-flex" }}>
            <CircularProgress
              thickness={4}
              size={100}
              variant="determinate"
              sx={{
                color: "#4F89A0",
                [`& .${circularProgressClasses.circle}`]: {
                  strokeLinecap: "round",
                },
              }}
              value={item.percentage}
            />
            <Box
              top={0}
              left={0}
              bottom={0}
              right={0}
              position="absolute"
              display="flex"
              alignItems="center"
              justifyContent="center"
            >
              <PercentageText>{item.percentage}%</PercentageText>
            </Box>
          </Box>
        </Grid>
      </Stack>
      <Grid container direction="row" className="support-amount-title">
        <AmountText>
          US$ {item.paid_amount}{" "}
          <SpanText> of US$ {item.total_amount}</SpanText>
        </AmountText>
      </Grid>

      <SeeMore onClick={seeMore}>See more</SeeMore>
    </ContainerRoot>
  );
};

export default Support;

const Label = styled(Typography)(() => ({
  color: "#212529",
  fontWeight: 600,
  fontSize: 20,
}));

const AmountText = styled("span")(() => ({
  color: "#4F89A0",
  fontSize: 24,
  fontWeight: "bold",
}));

const SeeMore = styled(Typography)(() => ({
  color: "#007AFF",
  fontSize: 20,
  fontWeight: 400,
  textAlign: "center",
  cursor: "pointer",
  paddingTop: 5,
}));

const PercentageText = styled(Typography)(() => ({
  color: "#A6CAD2",
  fontSize: 20,
  fontWeight: 600,
}));

const SpanText = styled("span")(() => ({
  color: "#343A40",
  fontWeight: 300,
  fontSize: 16,
}));
const ContainerRoot = styled(Stack)(() => ({
  backgroundColor: "#EEF8FF",
  borderRadius: 10,
  margin: 10,
  padding: 10,
}));
