import React from "react";

import { styled } from "@material-ui/styles";

import { Grid, InputAdornment, OutlinedInput } from "@material-ui/core";
import { Icon } from "@iconify/react";
import { Box } from "@material-ui/system";
import searchFill from "@iconify/icons-eva/search-fill";

const RootWrapperStyle = styled("div")(({ theme }) => ({
  borderRadius: 10,
  backgroundColor: "#f9f9f9",
}));

const SearchStyle = styled(OutlinedInput)(({ theme }) => ({
  "& fieldset": {
    borderWidth: `1px !important`,
    borderColor: `#ced4da !important`,
  },
  borderRadius: 10,
  // backgroundColor: "#ced4da",
  margin: 20,
  height: 33,
}));

interface SearchAppBarProps {
  value: string;
  onChange:
    | React.ChangeEventHandler<HTMLInputElement | HTMLTextAreaElement>
    | undefined;
  startSearch: React.MouseEventHandler<SVGSVGElement>;
}

export default function SearchAppBar({
  value,
  onChange,
  startSearch,
}: SearchAppBarProps) {
  return (
    <SearchStyle
      value={value}
      onChange={onChange}
      fullWidth
      placeholder="Search goals, friends, companies, donees or Ngo´s"
      style={{ fontSize: 14 }}
      endAdornment={
        <InputAdornment position="start">
          <Box
            onClick={startSearch}
            component={Icon}
            icon={searchFill}
            sx={{ color: "#99CAFF", height: 22, width: 22, cursor: "pointer" }}
          />
        </InputAdornment>
      }
    />
  );
}
