import React from "react";
import { IconButton } from "@material-ui/core";
import { RenderArrowProps } from "react-elastic-carousel";
import LeftArrowIcon from "../assets/icons/left-arrow.svg";
import RightarrowIcoon from "../assets/icons/right-arrow.svg";

const MyArrow = ({ type, onClick, isEdge }: RenderArrowProps) => {
  const pointer =
    type === "PREV" ? (
      <img src={LeftArrowIcon} alt="LeftArrowIcon" className="navIcons" />
    ) : (
      <img src={RightarrowIcoon} alt="RightarrowIcoon" className="navIcons" />
    );
  return (
    <IconButton sx={{}} onClick={onClick} disabled={isEdge}>
      {pointer}
    </IconButton>
  );
};
export default MyArrow;
