import React from "react";
import { Stack } from "@material-ui/core";
import { RenderPaginationProps } from "react-elastic-carousel";
import { useSpring, animated } from "@react-spring/web";

const CustomPagination = ({
  pages,
  activePage,
  onClick,
}: RenderPaginationProps) => {
  const styleNormal = {
    backgroundColor: "#c3cfe2",
    height: 8,
    width: 8,
    borderRadius: "50%",
    margin: 2,
    x: 0,
  };
  const styles = useSpring({
    loop: false,
    config: { duration: 600, friction: 20 },
    to: [
      {
        backgroundColor: "#f5576c",
        height: 10,
        width: 10,
        borderRadius: "50%",
        margin: 2,
      },
      {
        backgroundColor: "#f5576c",
        height: 11,
        width: 11,
        borderRadius: "50%",
        margin: 2,
      },
      {
        backgroundColor: "#46e891",
        height: 12,
        width: 12,
        borderRadius: "50%",
        margin: 2,
      },
    ],
    from: styleNormal,
  });

  return (
    <Stack
      direction="row"
      justifyContent="center"
      alignItems="center"
      pt={2}
      style={{ height: 25, minWidth: 300 }}
    >
      {pages.map((page, i) => {
        return (
          <animated.div
            onClick={() => onClick(i.toString())}
            key={i.toString()}
            style={activePage === i ? styles : styleNormal}
          />
        );
      })}
    </Stack>
  );
};

export default CustomPagination;
