import React from "react";
import { Stack, Typography } from "@material-ui/core";

import { PopularGoalsList } from "../../../../../utils/typeHelpers";
import { useNavigate } from "react-router";
import { styled } from "@material-ui/styles";

const ImageCard = ({ item }: { item: PopularGoalsList }) => {
  let navigate = useNavigate();
  return (
    <ContainerCard
      direction="column"
      justifyContent="flex-end"
      alignItems="flex-start"
      style={{
        backgroundImage: `url(${item.image})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        cursor: "pointer",
        borderRadius: 10,
      }}
      onClick={() => navigate(`../viewgoal/${item.slug}`)}
    >
      <div className="image-card-gradient-cnt">
        <Label variant="body1">{item.title}</Label>
        <Endorsed>Endorsed by</Endorsed>
        <Endorsed>{item.profile.ngo_username}</Endorsed>
      </div>
    </ContainerCard>
  );
};

export default ImageCard;

const ContainerCard = styled(Stack)(() => ({
  width: 120,
  height: 200,
  borderStyle: "solid",
  borderColor: "#9260cc #47afe4 #2e80b1 #5758c6",
  borderRadius: 10,
}));

const Label = styled(Typography)(() => ({
  color: "#CED4DA",
  fontSize: 14,
}));

const Endorsed = styled(Typography)(() => ({
  color: "#2A9DC1",
  fontSize: 10,
}));
