import { Container, Grid } from "@material-ui/core";

import React, { useState, useRef, useCallback, useEffect } from "react";
import { ErrorBoundary } from "react-error-boundary";
import { useQuery, UseQueryResult } from "react-query";
import { useSelector } from "react-redux";

import Loader from "../../../../../components/Loader";
import Scrollbar from "../../../../../components/Scrollbar";
import { GET_GOAL_LIST_API } from "../../../../../utils/Api";
import {
  GoalHomeType,
  GoalListApiResponse,
  RootAppState,
} from "../../../../../utils/typeHelpers";
import useAxios from "../../../../../utils/useAxios";
import PageErrorBoundary from "../../../../Errror/PageErrorBoundary";
import CustomScroller from "./CustomScroller";
import PostCard from "./PostCard";

import ErrorCatchComponent from "../../../../../components/ErrorCatchComponent";
import ApiErrorPage from "../../../../Errror/ApiErrorPage";

const RightColumn = () => {
  const userState = useSelector((state: RootAppState) => state.AuthReducer);
  const observer = useRef<IntersectionObserver>();
  const [explode, setExplode] = useState(false);
  const [hasMore, setHasMore] = useState(false);
  const [uri, setUri] = useState("");
  const [like, setLike] = useState(false);

  const queryGoals: UseQueryResult<GoalListApiResponse, Error> = useQuery(
    [
      "getGoalsList",
      [uri, userState.isLoggedIn ? userState.userInfo.access : "", like],
    ],
    () =>
      GET_GOAL_LIST_API(
        uri,
        userState.isLoggedIn ? userState.userInfo.access : ""
      ),
    {
      enabled: true, // turned off by default, manual refetch is needed
    }
  );
  const { data, isLoading } = queryGoals;

  const [posts, setPosts] = useState<GoalHomeType[] | []>([]);

  useEffect(() => {
    let cancel = false;

    if (posts.length > 0 && data?.results !== undefined && uri !== "") {
      setPosts((oldPosts) => [...oldPosts, ...data?.results]);
      if (data.results.length > data.count) setUri("");
    } else if (data?.results !== undefined) {
      setPosts(data?.results);
    }
    return () => {
      cancel = true;
    };
  }, [data, uri]);

  useEffect(() => {
    if (data) {
      setHasMore(data.count > posts.length);
    }
  }, [posts]);

  const lastBookElementRef = useCallback(
    (node) => {
      if (observer.current) observer.current?.disconnect();
      observer.current = new IntersectionObserver((entries) => {
        if (entries[0].isIntersecting && hasMore) {
          var str = data?.next;

          var res: RegExpMatchArray | null | undefined = str?.match(
            new RegExp("/goal-list/([^/]+)")
          );
          if (res) {
            const subUri: string = res[1];
            setUri(subUri);
          }
        }
      });
      if (node) observer.current.observe(node);
    },
    [isLoading, hasMore]
  );
  const handleLike = () => setLike(!like);

  return (
    <CustomScroller>
      <ErrorBoundary
        fallbackRender={({ error, resetErrorBoundary }) => (
          <PageErrorBoundary
            error={error}
            resetErrorBoundary={resetErrorBoundary}
          />
        )}
        onReset={() => setExplode(false)}
        resetKeys={[explode]}
      >
        {posts === [] ? (
          <Loader />
        ) : (
          <Grid
            container
            direction="column"
            style={{ padding: 20 }}
            justifyContent="center"
            alignItems="flex-end"
          >
            {posts !== undefined ? (
              posts.map((goalItem, index) => {
                if (posts?.length === index + 1) {
                  return (
                    <div ref={lastBookElementRef} key={`${index}`}>
                      <PostCard
                        keyy={`${index}`}
                        goalItem={goalItem}
                        handleLike={handleLike}
                      />
                    </div>
                  );
                } else {
                  return (
                    <div key={`${index}`}>
                      <PostCard
                        keyy={`${index}`}
                        goalItem={goalItem}
                        handleLike={handleLike}
                      />
                    </div>
                  );
                }
              })
            ) : (
              <div />
            )}
          </Grid>
        )}
      </ErrorBoundary>
    </CustomScroller>
  );
};

export default RightColumn;
